.hparsons-input {
  padding: 15px;
}
.hparsons-tip {
  font-style: italic;
}
.parsons-tooltip {
  visibility: hidden;
  max-width: 120px;
  width: max-content;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  top: 30px;
}
.parsons-block:hover .parsons-tooltip {
  visibility: visible;
}
.parsons-block {
  display: inline-block;
  font-family: monospace;
  border-color: gray;
  margin: 0 1px;
  position: relative;
  border-radius: 10px;
  background-color: #efefef;
  border: 1px solid #d3d3d3;
  padding: 5px 10px;
  margin-top: 5px;
}
.drop-area .parsons-block.incorrectPosition {
  background-color: #ffbaba;
  border: 1px solid red;
}
.parsons-block:hover,
.parsons-block:focus {
  border-color: black;
}
.drop-area {
  background-color: #ffa;
  padding: 0 5px 5px 5px;
  min-height: 32px;
  height: auto;
  margin: 2px 0;
}
.drop-area.incorrect {
  background-color: #f2dede;
  border-color: #f2b6b6;
}
.drop-area.correct {
  background-color: #dff0d8;
  border-color: #ade595;
}
.drag-area .parsons-block:hover .tooltip {
  visibility: visible;
}
.drag-area {
  background-color: #efefff;
  padding: 0 5px 5px 5px;
  min-height: 32px;
  height: auto;
  margin: 2px 0;
}
